<template>
    <div 
        @dblclick="fun_reminder1(task._id)"
        :class="task.reminder ? 'task_reminder': 'task'"
    >
        <h2> 
            {{task.text}} 
            <i @click="fun_del1(task._id)" class="fas fa-times"></i>
            <!--i.fas.times + enter -->
        </h2>
        <p> {{task.day}}</p>
    </div>
</template>

<script>
export default {
    name: 'Task',
    props:{
        task: Object,
    },
    methods:{
        fun_del1(id){
            this.$emit('event_del1', id)
        },
        fun_reminder1(id){
            this.$emit('event_reminder1', id)
        }
    },
    emits: ['event_del1', 'event_reminder1']
}
</script>

<style scope>
.fas {
    color: red;
}
.task {
    background: #f4f4f4;
    margin: 5px;
    padding: 10px 20px;
    box-sizing: border-box;
}
.task_reminder {
    background: #f4f4f4;
    margin: 5px;
    padding: 10px 20px;
    border-left: 15px solid green;
}
.task h2 {
    display:flex;
    align-items: center;
    justify-content: space-between;
}

.task_reminder h2 {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
</style>