<template>
  <div class="container">
    <Header title="Task Manager"/>
    <Tasks 
      @event_switch2='fun_switch2' 
      @event_del2='fun_del3'
      @event_add2='fun_add3'  
      @event_reminder2="fun_reminder3" 
      :tasks="tasks" 
      :isShowTask="isShowTask"
      />
    <Footer/>
  </div>
</template>

<script>
import Header from './components/Header.vue'
import Tasks from './components/Tasks.vue'
import Footer from './components/Footer.vue'
import TaskAPI from './TaskService.js'

export default {
  name: 'App',
  components: {
    Header,
    Tasks,
    Footer,
  },

  methods: {
    fun_switch2(){
      this.isShowTask = !this.isShowTask
    },

    async fun_del3(id){
      if (confirm('Are you sure to delete this item')){
        await TaskAPI.deleteTask(id);
        this.tasks = await TaskAPI.getTasks();
      }
    },
    
    async fun_add3(newTask){
      await TaskAPI.insertTask(newTask);
      this.tasks = await TaskAPI.getTasks();
    },

    async fun_reminder3(id){
      await TaskAPI.putTask_reminder(id);
      this.tasks = await TaskAPI.getTasks();
    }
  },

  data() {
    return {
      tasks: [],
      isShowTask: true,
      error: '',
    }
  },
  async created() {
    try {
      this.tasks = await TaskAPI.getTasks();
    } catch(err) {
      this.error = err.message;
    }
  }
}
</script>

