<template>
    <div>
        <Switch 
        @event_switch1="fun_switch2"
        :text="isShowTask ? 'OPEN' : 'CLOSE'"
        :isOpen="isShowTask"
        />
    </div>

    <div v-show="isShowTask">
        <AddTask @event_add1="fun_add2"/>
    </div>

    <div :key="task.id" v-for="task in tasks">
        <Task @event_del1="fun_del2" @event_reminder1="fun_reminder2" :task="task"/>
    </div>
</template>

<script>
import Switch from './Switch.vue'
import Task from "./Task.vue"
import AddTask from "./AddTask.vue"

export default {
    name: 'Tasks',
    components: {
        Task,
        AddTask,
        Switch,
    },
    props: {
        tasks: Array,
        isShowTask: Boolean,
    },
    methods: {
        fun_switch2(){
            this.$emit('event_switch2')
        },

        fun_del2(id){
            this.$emit('event_del2', id)
        },
        
        fun_add2(newTask){
            this.$emit('event_add2', newTask)
        },

        fun_reminder2(id){
            this.$emit('event_reminder2', id)
        }

    },
    emits: ['event_del2', 'event_add2', 'event_reminder2', 'event_switch2']
}
</script>