<template>
    <form class="form" @submit="fun_add1">
        <div class="form-text">
            <label> Task </label>
            <input v-model="text" type='text' name='task-name' placeholder="Add Task Name"> 
        </div>
        <br>
        <div class="form-text">
            <label> Time </label>
            <input v-model="day" type='text' name='task-time' placeholder="Add Task Time">
        </div>
        <br>
        <div class="form-checkbox">
            <label> Reminder </label>
            <input v-model="reminder" type='checkbox' name='task-reminder'>
        </div>

        <input class='btn_add' type='submit' value='Add new Task'>
    </form>
</template>

<script>
export default {
    name: 'newTask',
    data(){
        return {
            text: "",
            day:"",
            reminder:false
        }
    },
    methods:{
        fun_add1(event){
            event.preventDefault();
            
            if(!this.text || !this.day){
                alert("Please fill in the blank")
                return
            }

            let newTask={
                id: Math.floor(Math.random() * 10000),
                text: this.text,
                day: this.day,
                reminder: this.reminder
            }
            
            this.text=""
            this.day=""
            this.reminder=""

            this.$emit('event_add1', newTask)
        }
    },

    emits: ['event_add1']
}
</script>

<style scoped>
.form {
    margin-top: 20px;
    margin-bottom: 20px;
    font-size: 20px
}
.form-text {
  margin: 0px;
}
.form-text label {
  display: block;
}
.form-text input {
  width: 90%;
  height: 40px;
  margin-top: 15px;
  padding: 3px 7px;
  font-size: 17px;
}
.form-checkbox {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.form-checkbox input {
    flex: 2;
    height: 20px;
}

.btn_add {
    width: 100%;
    display: inline-block;
    background: #000;
    color: #fff;
    border: none;
    padding: 15px 15px;
    margin-top: 15px;
    margin-bottom: 15px;
    border-radius: 5px;
    cursor: pointer;
    text-decoration: none;
    font-size: 15px;
    font-family: inherit;
}
</style>