import axios from 'axios';

const url = 'api/task';

class TaskAPI {
    //Get Task
    static async getTasks() {
        const res = await axios.get(url)
        try {
          const data = res.data;
          return data;
        } catch (err) {
          return err;
        }
    }

    //Create Task
    static async insertTask(newTask) {
        return await axios.post(url, newTask);
    }

    //Delete task
    static async deleteTask(id) {
      const res = await axios.delete(`${url}/${id}`);
      try {
          const data = res.data;
          return data;
        } catch (err) {
          return err;
        }
    }

    //Switch reminder of task
    static async putTask_reminder(id) {
      const res = await axios.put(`${url}/reminder/${id}`)
      try {
        const data = res.data;
        return data;
      } catch (err) {
        return err;
      }
    }
}

export default TaskAPI;

