<template>
    <button 
        :class="isOpen?'btn_open':'btn_close'"
        @click='fun_switch1'>
        {{text}}
    </button>
</template>

<script>
export default {
    name: 'Switch',
    props: {
        text: String,
        isOpen: Boolean,
    },
    methods:{
        fun_switch1(event) {
            event.preventDefault();
            this.$emit('event_switch1')
        }
    },
    emits: ['event_switch1']
}
</script>

<style>
.btn_open {
    display: inline-block;
    width: 100%;
    background: green;
    color: #fff;
    border: none;
    padding: 10px 20px;
    margin-top: 25px;
    border-radius: 5px;
    cursor: pointer;
    text-decoration: none;
    font-size: 15px;
    font-family: inherit;
}

.btn_close {
    display: inline;
    width: 100%;
    background: red;
    color: #fff;
    border: none;
    padding: 10px 20px;
    margin-top: 25px;
    border-radius: 5px;
    cursor: pointer;
    text-decoration: none;
    font-size: 15px;
    font-family: inherit;
}
</style>